
















































































































































































































































































































































































































































import { Component, Vue, Watch } from 'vue-property-decorator';
import VueRecaptcha from 'vue-recaptcha';
import InfoSide from '@/components/InfoSide.vue';
import VuePhoneNumberInput from 'vue-phone-number-input';
import SvgIcon from '@/components/utilities/SvgIcon.vue';
import { User } from '@/modules/auth/interfaces/user.interface';
import { countries, auth } from '@/store/namespaces';
import {
    NationalityCountry,
} from '@/modules/countries/interfaces/countries.interface';
import CountriesTypes from '@/store/countries/methods/countries.methods';
import AuthTypes from '@/store/auth/methods/auth.methods';
import { required, email, minLength, sameAs } from 'vuelidate/lib/validators';
import { validationMixin } from 'vuelidate';
import {
  getSpanishLanCountries,
    showToast,
    splitFullName
} from '@/utils/global-functions';
import { getDMY } from '@/utils/date-functions';
import { GENDERS } from '@/utils/collections';
import { USER_EMPTY_STATE } from '@/modules/auth/emptyStates/user.emptyState';
import Toast from '@/components/utilities/Toast.vue';
import TermsPopup from '@/modules/auth/components/TermsPopup.vue';
import Loader from '@/components/utilities/Loader.vue';

@Component({
    components: {
        VueRecaptcha,
        InfoSide,
        VuePhoneNumberInput,
        SvgIcon,
        Toast,
        TermsPopup,
        Loader
    },
    mixins: [validationMixin],
    validations: {
        fullName: { required },
        fullLastName: { required },
        user: {
            email_user: { required, email },
            password: {
                required,
                minLength: minLength(8),
                containsUppercase: function (value) {
                    return /[A-Z]/.test(value);
                },
                containsNumber: function (value) {
                    return /[0-9]/.test(value);
                },
            },
            referral_node: {},
            gender: { required },
            main_phone: { required },
            main_phone_wha: { required },
            name_nationality_country: { required },
            ok_legal_terms: { accepted: (val) => val === true },
        },
        repeatPassword: { required, sameAs: function (value){
                //@ts-ignore
                return value === this.user.password
        }},
    },
})
export default class LevelCero extends Vue {
    name = '';
    age = 0;
    submitStatus = null;
    fullName = '';
    fullLastName = '';
    user: Partial<User> = USER_EMPTY_STATE;
    repeatPassword: string = '';
    registerButton = {
        name: 'Iniciar sesin',
        text: 'Ya tienes una cuenta?',
        level: 2,
        redirect: '/inicio-sesion',
    };
    genders = GENDERS;
    currentGender = '';
    termsModal = false;
    currentNacionalityCountry = '';
    mainTranslations: any = {
        countrySelectorLabel: 'Cdigo de pas',
        countrySelectorError: 'Elige un pas',
        phoneNumberLabel: '',
        example: 'Ejemplo :',
    };
    $refs : any ={}
    dropdownCountries: any = ['VE'];
    spanishTranslateCountries = [];
    recaptchaKey = process.env.VUE_APP_RECAPTCHA_KEY;
    captchaRendered = false;

    async mounted() {
        this.$refs.startLoader.showLoader();
        await this.fetchAllNationalityCountries();
        this.spanishTranslateCountries = getSpanishLanCountries(this.nationalityCountries)
        this.dropdownCountries.push(this.userData.name_resid_country);
        this.user = this.userData;
        this.$refs.startLoader.hideLoader();
    }

    /* MODAL */
    changeTermsModalStatus(newVal: boolean) {
        this.termsModal = newVal;
    }

    /* END MODAL */

    verifyMethod(id: string){
        this.user.captcha = id;
    }

    expiredMethod(){
        this.$refs.recaptcha.reset();
    }

    renderMethod(){
        this.captchaRendered = true;
    }

    errorMethod(el: any){
        showToast('Error verificando el reCaptcha','error')
        this.$refs.recaptcha.reset();
    }

    async submit() {
        this.$v.$touch();
        console.log(this.$v)
        if (!this.$v.$invalid && this.user.captcha !== undefined) {
            this.$refs.loader.showLoader();
            let countryNatId = await this.fetchCountryIdByName(
                this.user.name_nationality_country
            );
            this.user.cod_user_serv_public = 'TEMP9202DKKD';
            this.user.date_legacy_reg = getDMY(new Date());
            if (countryNatId.length ) { //&& countryResidId.length
                this.user.id_nationality_country = countryNatId[0].id_ip_country;
                let registerResponse : {errorMessage: string} | boolean = await this.createClient(this.user);
                this.$refs.loader.hideLoader();
                if (registerResponse === true) {
                    this.$router.push('/registro/verificacion');
                } else if (typeof registerResponse !== 'boolean') {
                    showToast(registerResponse.errorMessage,'error')
                }
            } else {
                this.$refs.loader.hideLoader();
                showToast('Ha ocurrido un error al crear el usuario','error')
            }
            this.$refs.recaptcha.reset();
        }
        else if (this.user.captcha === undefined)
            showToast('Por favor complete el reCaptcha abajo','error')
        this.$refs.recaptcha.reset();
    }


    updateMainPhone(payload: any) {
            this.user.main_phone = payload.phoneNumber;
            this.user.main_phone_code = '+' + payload.countryCallingCode;
            this.user.main_phone_full = payload.formattedNumber;
            if (this.user.main_phone.charAt(0) === '0') {
                this.user.main_phone =this.user.main_phone.substring(1);;
            } 
            this.user.main_phone_full = '+'+ payload.countryCallingCode + this.user.main_phone;
    }


    @Watch('fullName')
    getFirstAndSecondName(newVal: string) {
        let names = splitFullName(newVal);
        this.user.first_name = names.firstName;
        this.user.second_name = names.secondName;
    }
    @Watch('fullLastName')
    getFirstAndSecondLastName(newVal: string) {
        let names = splitFullName(newVal);
        this.user.last_name = names.firstName;
        this.user.second_last_name = names.secondName;
    }

    @countries.Action(CountriesTypes.actions.FETCH_ALL_NATIONALITY_COUNTRIES)
    fetchAllNationalityCountries!: () => Promise<boolean>;
    @countries.Getter(CountriesTypes.getters.GET_ALL_NATIONALITY_COUNTRIES)
    nationalityCountries!: NationalityCountry[];
    @countries.Action(CountriesTypes.actions.FETCH_COUNTRY_ID_BY_NAME)
    fetchCountryIdByName!: (countryName: string) => Promise<any>;
    @countries.Action(CountriesTypes.actions.FETCH_RESID_COUNTRY_ID_BY_NAME_CLIENT)
    fetchResidCountryIdByName!: (countryName: string) => Promise<any>;
    @auth.Action(AuthTypes.actions.CREATE_CLIENT)
    createClient!: (user: Partial<User>) => Promise<{errorMessage: string} | boolean>;
    @auth.Getter(AuthTypes.getters.GET_REGISTER_USER_DATA)
    userData!: Partial<User>;
}
